* {
  margin: 0;
  bottom: 0;
}

.App {
  color: #100e0f;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: auto;
  width: auto;
  /* background-color: #f8f7f3; */
  background-color: #F2F5F6;
  border: solid white 30px;
  font-family: "Domine", serif;
  /* font-size: calc(10px + 1.3vmin); */
  /* font-size: calc(16px + 6 * ((100vw - 320px) / 680)); */
}
a{
  text-decoration: none;
  font-weight: 800;
  color:#859FA3;
}
a:visited {
  color:#859FA3;
}

a:selection{
color: #859FA3;
}

a:hover{
  color:#859FA3;
}

.NavbarContent {
  height: 10vh;
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.asbLogo {
  height: 10vh;
}

.logo {
  object-fit: cover;
  height: 100%;
  width: 100%;
}
.content {
  height: auto;
  max-width: 40rem;
  margin-right: auto;
  margin-left: auto;
  /* background-color: #f4c3b4; */
}
.aboutContent {
  display: block;
  margin-top: 1.5em;
  margin-bottom: 2em;
  font-size: 23px;
  font-size: 1.4375rem;
  margin-right: auto;
  margin-left: auto;
  /* background-color: azure; */
}

.aboutContent p {
  font-size: 2rem;
  line-height: 1.3;
  color: #3d3971;
}

.projects {
  /* background-color: yellow; */
  display: flex;
  flex-direction: column;
  height: auto;
  padding-top: 3rem;
}

.list-section {
  max-width: 33rem;
  line-height: 1.25;
  font-size: 16px;
  font-size: 1rem;
  margin-right: auto;
  margin-left: auto;
}

h2 span {
  text-transform: uppercase;
  font-family: Arial, Helvetica, sans-serif;
  color: #b68316;
}

.title{
text-decoration: underline 0.15em  #2d9eef;
text-underline-offset: 0.2em;
transition: text-decoration-color 300ms, text-underline-offset 300ms;
line-height: 2;
font-weight: 600;
}